@import "src/app/styles/mixins/mixins";

.button {
  @include FlexCenter(row, center);
  @include FontStyle(500, 16px, 20px);

  gap: 5px;
  border: none;
  min-height: 36px;
  width: 100%;

  background: var(--secondary-color);
  box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  border-radius: 30px;
  color: #ffffff;
}

.red {
  background: #ee1d3d;
  color: #ffffff;
}

.disabled {
  background: #004d99;
  opacity: 0.5;
  color: rgba(0, 46, 92, 1);
}

.secondary {
  @include FontStyle(500, 16px, 20px);
  letter-spacing: 0.01em;
  color: #000000;
  border-radius: 30px;
  background-color: white;
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 2px 10px rgba(109, 109, 109, 0.25));
}
